.select-css {
	display: block;
  font-size: 35px;
	font-family: sans-serif;
	font-weight: 700;
	color: rgb(154, 219, 228);
	line-height: 1.3;
	padding: .6em 1.4em .5em .8em;
	width: 100%;
	max-width: 100%;
	box-sizing: border-box;
	margin: 0;
	border: 1px solid #aaa;
	box-shadow: 0 1px 0 1px rgba(0,0,0,.04);
	border-radius: .5em;
	-moz-appearance: none;
	-webkit-appearance: none;
	appearance: none;
	background-color: rgb(19, 18, 18);
	background-repeat: no-repeat, repeat;
	background-position: right .7em top 50%, 0 0;
	background-size: .65em auto, 100%;
}
.select-css::-ms-expand {
	display: none;
}
.select-css:hover {
	border-color: rgb(154, 219, 228);
}
.select-css:focus {
	color:  rgb(154, 219, 228);
	outline: none;
}
.select-css option {
	font-weight:normal;
}








.button {
  border: none;
  color: white;
  padding: 16px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  transition-duration: 0.4s;
  cursor: pointer;
}

.button {
  background-color: rgb(0, 0, 0); 
  color: rgb(154, 219, 228); 
  border: 2px solid rgb(154, 219, 228);;
  border-radius: 8px;
}

.button:hover {
  background-color: rgb(94, 151, 158);;
  color: white;
}

.checkbox {
  margin-top: 20px;
  width: 30px;
  height: 30px;
  border-radius: 20px;
}


.manito {
  color: rgb(154, 219, 228); 
  font-size: 40px;
}

p {
  font-size: 30px;
}

.error {
  width: 100%;
  height: 30px;
  color: rgb(182, 36, 36);
  /* background-color: rgba(255, 133, 133, 0.733); */
}

.input {
  border-radius: 8px;
  font-size: 16px;
  outline: none;
}